.block{display:flex;flex:1;margin:0 20px;text-align:center}.box__wrapper{position:relative;margin-top:40px;border-radius:10px;padding:20px;border:1px solid #e5e5e5;flex-direction:column}.box__title{display:flex;align-items:center;position:absolute;left:50%;top:0;background:#fff;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);padding:0 10px;font-weight:700}.result-block__title-img{width:24px;height:24px;margin-right:5px;margin-left:-5px;background-image:url(https://i.loli.net/2019/02/05/5c593c85b4852.png);background-repeat:no-repeat;background-position:50%;background-size:contain}.result-block__goods-wrapper{display:flex;flex-direction:row;align-items:center;font-size:15px;background-color:#f6f6f6;padding:10px;border-radius:5px;margin-top:15px}.result-block__goods-image{width:80px;height:80px;background-image:url(https://i.loli.net/2019/02/05/5c5869d5a33e6.png);background-repeat:no-repeat;background-position:50%;background-size:contain}.result-block__goods-side{margin-left:10px;text-align:left}.result-block__goods-2nd-title{font-size:12px;color:#b2b2b2}.result-block__result{display:flex;flex-direction:row;justify-content:space-between;align-items:center;margin-top:20px}.result-block__lucky-code{height:50px;width:74px;background-image:url(https://i.loli.net/2019/02/05/5c593abd822b3.png);background-repeat:no-repeat;background-position:50%;background-size:contain}.result-block__result-right{display:flex;flex-direction:row;align-items:center}.result-block__right-arrow{width:12px;height:12px;margin-left:5px;background-image:url(https://i.loli.net/2019/02/05/5c5995d407358.png);background-repeat:no-repeat;background-position:50%;background-size:contain}