.block{display:flex;flex:1;margin:0 20px;text-align:center}.box__wrapper{position:relative;margin-top:40px;border-radius:10px;padding:20px;border:1px solid #e5e5e5;flex-direction:column}.box__title{display:flex;align-items:center;position:absolute;left:50%;top:0;background:#fff;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);padding:0 10px;font-weight:700}.lucky-code__list{display:flex;overflow:auto;margin-top:25px}.lucky-code__img{width:70px;height:50px;margin-right:10px;background-image:url(https://i.loli.net/2019/02/05/5c5986d7b825e.png);background-repeat:no-repeat;background-position:50%;background-size:contain}.lucky-code__img--multiple{background-image:url(https://i.loli.net/2019/02/05/5c5995d408e6d.png)}.lucky-code__item{padding-top:5px}.lucky-code__item:first-child{padding-left:20px}.lucky-code__item:last-child{padding-right:20px}.lucky-code__num{font-size:12px;text-align:center}.lucky-code__line{position:relative;height:2px;background-color:#d3d3d3;margin:5px 0 10px}.lucky-code__circle{position:absolute;left:50%;top:50%;margin-left:-5px;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);width:10px;height:10px;border-radius:50%;border:2px solid #d3d3d3;background-color:#fff}.lucky-code__ticket-num-wrapper{position:relative}.lucky-code__ticket-num{position:absolute;top:-5px;right:0;font-size:14px;font-weight:700;padding:0 7px;border-radius:15px;background:#000;color:#fff;border:2px solid #fff}