.block{display:flex;flex:1;margin:0 20px;text-align:center}.box__wrapper{position:relative;margin-top:40px;border-radius:10px;padding:20px;border:1px solid #e5e5e5;flex-direction:column}.box__title{display:flex;align-items:center;position:absolute;left:50%;top:0;background:#fff;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);padding:0 10px;font-weight:700}.ad-list{flex-direction:column}.ad-list__item{position:relative;margin:10px 0}.ad-list__btn{position:absolute;bottom:25px;left:20px;right:20px;padding:10px;border-radius:20px;font-size:15px;font-weight:700;color:#fff;background:#000}.ad-list__title{margin-bottom:20px;font-size:16px;font-weight:700}.hr{margin:25px 0;height:1px;background:#e5e5e5}.container.widthFix_container{width:auto;height:auto}.container.widthFix_container img{max-width:100%;height:auto}