.block{display:flex;flex:1;margin:0 20px;text-align:center}.box__wrapper{position:relative;margin-top:40px;border-radius:10px;padding:20px;border:1px solid #e5e5e5;flex-direction:column}.box__title{display:flex;align-items:center;position:absolute;left:50%;top:0;background:#fff;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);padding:0 10px;font-weight:700}.count-down__wrapper{display:flex;flex-direction:column}.countdown__title{font-weight:700;font-size:14px;margin-bottom:5px}.countdown__desc{font-size:12px;color:#999}.count-down__wrapper{margin-top:30px}.countdown__clock{display:flex;flex-direction:row;margin:20px 0;justify-content:center}.countdown__clock-item{font-size:30px;line-height:64px}.countdown__clock-item:nth-child(2n){margin:0 10px}.countdown__clock-item:nth-child(odd){width:64px;height:64px;background:#000;border-radius:8px;color:#fff;font-weight:700}